<template>
    <div id="quotes">
        <table class="all" v-if="normsList.length != 0">
            <tr>
                <th v-for="item in normsList" class="norm_fix">
                    {{ item.specName }}
                </th>
                <th class="n_promotion">
                    <span><i>*</i>促销价格</span>
                    <el-checkbox class="pro_price_checked" v-model="proPriceCheckedState" @change="proPriceChecked">全部相同</el-checkbox>
                </th>
                <th class="n_integral">
                    <i>*</i>积分最高抵用金额
                </th>
                <th class="n_original">
                    <span><i>*</i>原价</span>
                    <el-checkbox class="old_price_checked" v-model="oldPriceCheckedState" @change="oldPriceChecked">全部相同</el-checkbox>
                </th>
                <th class="n_commodity">
                    <i>*</i>是否有该规格商品
                </th>
                <th class="n_image">
                    匹配图片
                </th>
            </tr>
            <tr v-for="(row, num) in doneQuotesList.specPrices">
                <!--规格-->
                <td v-for="(item, index) in normsList" v-if="index == 0 && num % rowspan == 0" :rowspan="rowspan">
                    {{ row.specValues[index] }}
                </td>
                <td v-else-if="index == 1 && num * normsList[1].specValues.length % rowspan == 0" :rowspan="rowspan/normsList[1].specValues.length">
                    {{ row.specValues[index] }}
                </td>
                <td v-else-if="index == 2">
                    {{ row.specValues[index] }}
                </td>
                <!--促销价格-->
                <td>
                    <el-input
                        size="mini"
                        placeholder="请输入内容"
                        v-model.trim="row.proPrice"
                        @blur="isNumPrice(row.proPrice, num)"
                        :disabled="!row.isExist || (num == 0 ? false : proPriceCheckedState)"
                        :maxlength="8"
                        style="width: 80%">
                    </el-input>
                </td>
                <!--积分最高抵用金额-->
                <td>
                    <el-input
                        size="mini"
                        placeholder="请输入内容"
                        v-model.trim="row.usedIntegral"
                        @blur="isNumIntegral(row.usedIntegral, num)"
                        :disabled="!row.isExist"
                        :maxlength="8"
                        style="width: 80%">
                    </el-input>
                </td>
                <!--原价-->
                <td>
                    <el-input
                        size="mini"
                        placeholder="请输入内容"
                        v-model.trim="row.marketPrice"
                        @blur="isNumOldPrice(row.marketPrice, num)"
                        :disabled="!row.isExist || (num == 0 ? false : oldPriceCheckedState)"
                        :maxlength="8"
                        style="width: 80%">
                    </el-input>
                </td>
                <!--是否有该规格商品-->
                <td>
                    <el-checkbox v-model="row.isExist" @change="isCommoditychecked(row.isExist, num)"></el-checkbox>
                </td>
                <!--匹配图片-->
                <td>
                    <el-upload
                        class="upload-demo"
                        action=""
                        :on-change="upNormsPic"
                        :show-file-list="false"
                        :auto-upload="false"
                        style="display: inline-block">
                        <el-button type="text" :disabled="!row.isExist" @click="nowRowIndex=num">{{ row.picId === '' ? '点击上传' : '更改图片'}}</el-button>
                        <!--<button :disabled="!row.isExist" @click="nowRowIndex=num" style="border: 0 none; background: #fff;">{{ row.picId === '' ? '点击上传' : '更改图片'}}</button>-->
                    </el-upload>
                    <el-button type="text" :disabled="!row.isExist" @click="showPictureCardPreview(num)">查看</el-button>
                    <el-dialog
                        v-model="dialogFull"
                        size="tiny"
                        :modal-append-to-body=false
                        @open="$store.commit('openZindexModel')"
                        @close="$store.commit('closeZindexModel')">
                        <img width="100%" :src="fullUrl" alt="">
                    </el-dialog>
                </td>
            </tr>
        </table>
        <table class="only" v-else>
            <tr>
                <th class="n_promotion">
                    <span><i>*</i>促销价格</span>
                </th>
                <th class="n_integral">
                    <i>*</i>积分最高抵用金额
                </th>
                <th class="n_original">
                    <span><i>*</i>原价</span>
                </th>
            </tr>
            <tr v-for="(row, num) in doneQuotesList.specPrices">
                <!--促销价格-->
                <td>
                    <el-input
                        size="mini"
                        placeholder="请输入内容"
                        v-model.trim="row.proPrice"
                        @blur="isNumPrice(row.proPrice, num)"
                        :maxlength="8"
                        style="width: 80%">
                    </el-input>
                </td>
                <!--积分最高抵用金额-->
                <td>
                    <el-input
                        size="mini"
                        placeholder="请输入内容"
                        v-model.trim="row.usedIntegral"
                        @blur="isNumIntegral(row.usedIntegral, num)"
                        :maxlength="8"
                        style="width: 80%">
                    </el-input>
                </td>
                <!--原价-->
                <td>
                    <el-input
                        size="mini"
                        placeholder="请输入内容"
                        v-model.trim="row.marketPrice"
                        @blur="isNumOldPrice(row.marketPrice, num)"
                        :maxlength="8"
                        style="width: 80%">
                    </el-input>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                count: 0,                                       //计数， 初次加载 watch不触发， 第二次会触发
                normsList: this.norms,
                doneQuotesList: {                           //总的规格信息
                    specIds: [],
                    specNames: [],
                    specPrices: [
                        {
                            proPrice: '',
                            usedIntegral: '',
                            marketPrice: ''
                        }
                    ]
                },
                rowspan: 0,                                     //是合并一个大行的单元格 数量, 只是有第一种规格属性值的数量而已，单个的
                proPriceCheckedState: false,                //促销价格复选框
                oldPriceCheckedState: false,                //原价复选框
                onlyQuotes: {                               //没有规格属性的情况下的单个值
                    proPrice: '',
                    usedIntegral: '',
                    marketPrice: ''
                },
                fullUrl: '',
                dialogFull: false,
                nowRowIndex: 0                      //点击图片上传时为第几行
            }
        },
        props: ['norms', 'copyQuotesInfo'],
        created() {
            //新增初始无规格 count为0， 复选框处不点又直接曲儿 count+1，watch还是执行count<1中，会不正确。传入的copyQuotesInfo改为此组件的doneQuotesList那么编辑回显count初始>1 会有bug，所以事新增初始化count+1
            if(this.$route.query.goodId === '') {
                    this.count++
            }
            this.$emit('update:doneQuotesList', this.doneQuotesList)
        },
        methods: {
            createNormsTab(len) {
                let list = []
                for(let i=0; i<len; i++) {
                    let dict = {
                        specValues: [],         //商品规格值 组合
                        proPrice: '',           //促销价格
                        usedIntegral: '',       //积分抵用金额
                        marketPrice: '',        //市场价格
                        isExist: true,          //是否有该规格产品
                        picId: '',               //静态图片url id
                        url: '',                  //图片路径
                        baseUrl: '',             //本地图片路径
                    }
                    list.push(dict)
                }
                return {
                    specIds: [],
                    specNames: [],
                    specPrices: list
                }
            },
            //促销价格
            isNumPrice(vlaue, index) {
                if(vlaue === '') return
                let v = parseFloat(vlaue) + ''
                if(!/^\d+(\.\d+)?$/.test(vlaue)) {
                    this.doneQuotesList.specPrices[index]['proPrice'] = ''
                    this.$message({
                        message: '请输入数字',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else if(v === '0') {
                    this.doneQuotesList.specPrices[index]['proPrice'] = ''
                    this.$message({
                        message: '不能为0',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else if(v.length > 5) {
                    this.doneQuotesList.specPrices[index]['proPrice'] = ''
                    this.$message({
                        message: '不能超过5位',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else {
                    this.doneQuotesList.specPrices[index]['proPrice'] = (+vlaue).toFixed(2)
                    if(this.proPriceCheckedState) { //当被全选时  第一个值输入 失焦全改
                        for(let i=0, len=this.doneQuotesList.specPrices.length; i<len; i++) {
                            this.doneQuotesList.specPrices[i].proPrice = this.doneQuotesList.specPrices[0].proPrice
                            if(this.doneQuotesList.specPrices[i]['usedIntegral'] === '') {
                                this.doneQuotesList.specPrices[i]['usedIntegral'] = this.doneQuotesList.specPrices[i].proPrice
                            }
                        }
                    }
                    else {
                        if(this.doneQuotesList.specPrices[index]['usedIntegral'] === '') {
                            this.doneQuotesList.specPrices[index]['usedIntegral'] = this.doneQuotesList.specPrices[index]['proPrice']
                        }
                    }
                }
            },
            //积分抵用金额
            isNumIntegral(vlaue, index) {
                if(vlaue === '') return
                let v = parseFloat(vlaue) + ''
                if(!/^\d+(\.\d+)?$/.test(vlaue)) {
                    this.doneQuotesList.specPrices[index]['usedIntegral'] = ''
                    this.$message({
                        message: '请输入数字',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else if(v.length > 5) {
                    this.doneQuotesList.specPrices[index]['usedIntegral'] = ''
                    this.$message({
                        message: '不能超过5位',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else {
                    this.doneQuotesList.specPrices[index]['usedIntegral'] = (+vlaue).toFixed(2)
                }
            },
            //市场价格
            isNumOldPrice(vlaue, index) {
                if(vlaue === '') return
                let v = parseFloat(vlaue) + ''
                if(!/^\d+(\.\d+)?$/.test(vlaue)) {
                    this.doneQuotesList.specPrices[index]['marketPrice'] = ''
                    this.$message({
                        message: '请输入数字',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else if(v === '0') {
                    this.doneQuotesList.specPrices[index]['marketPrice'] = ''
                    this.$message({
                        message: '不能为0',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else if(v.length > 5) {
                    this.doneQuotesList.specPrices[index]['marketPrice'] = ''
                    this.$message({
                        message: '不能超过5位',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else {
                    this.doneQuotesList.specPrices[index]['marketPrice'] = (+vlaue).toFixed(2)
                    if(this.oldPriceCheckedState) {
                        for(let i=1, len=this.doneQuotesList.specPrices.length; i<len; i++) {
                            this.doneQuotesList.specPrices[i].marketPrice = this.doneQuotesList.specPrices[0].marketPrice
                        }
                    }
                }
            },
            //促销价格复选框
            proPriceChecked() {
                if(this.proPriceCheckedState) {
                    for(let i=0, len=this.doneQuotesList.specPrices.length; i<len; i++) {
                        if(i > 0) {
                            this.doneQuotesList.specPrices[i].proPrice = this.doneQuotesList.specPrices[0].proPrice
                        }
                        if(this.doneQuotesList.specPrices[i].usedIntegral === '') {
                            this.doneQuotesList.specPrices[i].usedIntegral = this.doneQuotesList.specPrices[i].proPrice
                        }
                    }
                }
            },
            //原价复选框
            oldPriceChecked() {
                if(this.oldPriceCheckedState) {
                    for(let i=1, len=this.doneQuotesList.specPrices.length; i<len; i++) {
                        this.doneQuotesList.specPrices[i].marketPrice = this.doneQuotesList.specPrices[0].marketPrice
                    }
                }
            },
            //是否有商品复选框
            isCommoditychecked(bl, index) {
                if(bl) {
                    if(this.proPriceCheckedState) {
                        this.doneQuotesList.specPrices[index]['proPrice'] = this.doneQuotesList.specPrices[0]['proPrice']
                    }
                    if(this.oldPriceCheckedState){
                        this.doneQuotesList.specPrices[index]['marketPrice'] = this.doneQuotesList.specPrices[0]['marketPrice']
                    }
                }
                else {
                    this.doneQuotesList.specPrices[index]['proPrice'] = ''
                    this.doneQuotesList.specPrices[index]['usedIntegral'] = ''
                    this.doneQuotesList.specPrices[index]['marketPrice'] = ''
                    this.doneQuotesList.specPrices[index]['picId'] = ''
                    this.doneQuotesList.specPrices[index]['url'] = ''
                    this.doneQuotesList.specPrices[index]['baseUrl'] = ''
                }
            },
            //图片上传
            upNormsPic(file, fileList) {
                let isJpgPng = (file.raw.type === 'image/jpeg') || (file.raw.type === 'image/png')
                let isLt2M = file.size / 1024 / 1024 < 2;
                if (!isJpgPng) {
                    this.$message({
                        message: '注意! 上传的图片只能是JPG或者PNG',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                if (!isLt2M) {
                    this.$message({
                        message: '注意! 上传的图片大小不能超过 2MB!',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                //加载图片获取图片真实宽度和高度
                let image = new Image();
                image.onload = () => {
                    let isSize
                    let width = image.width;
                    let height = image.height;
                    //图片的尺寸比0.8 -- 1.2  主图
                    isSize = width/height
                    isSize = 0.8 < isSize && isSize < 1.2 && width > 360 && height > 360
                    if(!isSize) {
                        this.$message({
                            message: '注意! 上传的图片宽高比要接近1：1,并且尺寸大于360',
                            duration: 1000,
                            type: 'warning'
                        });
                        return false
                    }
                    let formData = new FormData();
                    formData.append("file", file.raw);
                    this.$http({
                        url: '/photosController/addPhotos.do',
                        method: 'post',
                        data: formData
                    }).then( res => {
                        if(res.data.success) {
                            this.doneQuotesList.specPrices[this.nowRowIndex].picId = res.data.data.fileid
                            this.doneQuotesList.specPrices[this.nowRowIndex].url = res.data.data.fileurl
                            this.doneQuotesList.specPrices[this.nowRowIndex].baseUrl = file.url
                            this.$message({
                                message: '图片上传成功',
                                duration: 1000,
                                type: 'success'
                            });
                        }
                        else {
                            this.$message({
                                message: '图片上传失败',
                                duration: 1000,
                                type: 'error'
                            });
                        }
                    }).catch( error => {
                        console.log(error)
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                    })
                };
                image.src= file.url;
            },
            //图片显示
            showPictureCardPreview(num) {
                if(this.doneQuotesList.specPrices[num].baseUrl === '') {
                    this.$message({
                        message: '注意! 请确保上传了图片。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.fullUrl = this.doneQuotesList.specPrices[num].baseUrl
                this.dialogFull = true;
            }
        },
        watch: {
            //对于norms不管新增还是编辑回显 如果回显没规格属性 初始count为0 如果有规格属性count为1， 所以一开始都不能判断设置count不能>1
            norms: {
                handler:function (newValue, oldValue) {
                    this.normsList = newValue
                    if(this.normsList.length == 0) {   //没有规格属性的单个情况下 独立  每次初始化
                        this.doneQuotesList = {
                            specIds: [],
                            specNames: [],
                            specPrices: []
                        }
                    }
                    else if(this.normsList.length == 1) {   //只有一个规格属性的情况
                        this.rowspan = 1
                        let one = this.normsList[0].specValues.length
                        this.doneQuotesList = this.createNormsTab(one)
                    }
                    else if(this.normsList.length == 2) {   //2个规格属性
                        this.rowspan = this.normsList[1].specValues.length
                        let one = this.normsList[0].specValues.length
                        let two = this.normsList[1].specValues.length
                        this.doneQuotesList = this.createNormsTab(one * two)
                    }
                    else if(this.normsList.length == 3) {       //3个规格属性
                        this.rowspan = this.normsList[1].specValues.length * this.normsList[2].specValues.length
                        let one = this.normsList[0].specValues.length
                        let two = this.normsList[1].specValues.length
                        let three = this.normsList[2].specValues.length
                        this.doneQuotesList = this.createNormsTab(one * two * three)
                    }
                    this.count++
                },
                deep:true
            },
            doneQuotesList(newValue, oldValue) {
                if(this.count > 1) {        //count  规格初入 赋值改变  监听
                    let list = []
                    if(this.normsList.length == 0) {
                        for(let item in this.onlyQuotes)  {
                            this.onlyQuotes[item] = ''
                        }
                        this.doneQuotesList.specPrices.push(this.onlyQuotes)
                        this.$emit('update:doneQuotesList', this.doneQuotesList)
                        return                      //如果是没有属性值 就不执行下面的
                    }
                    else if(this.normsList.length == 1) {   //1
                        this.doneQuotesList.specIds = [this.normsList[0].specId]
                        this.doneQuotesList.specNames = [this.normsList[0].specName]
                        for(let i=0; i<this.normsList[0].specValues.length; i++) {
                            list.push({
                                specValues: [this.normsList[0].specValues[i]],
                            })
                        }
                    }
                    else if(this.normsList.length == 2) {   //2
                        this.doneQuotesList.specIds = [this.normsList[0].specId, this.normsList[1].specId]
                        this.doneQuotesList.specNames = [this.normsList[0].specName, this.normsList[1].specName]
                        for(let i=0; i<this.normsList[0].specValues.length; i++) {
                            for(let j=0; j<this.normsList[1].specValues.length; j++) {
                                list.push({
                                    specValues: [this.normsList[0].specValues[i], this.normsList[1].specValues[j]],
                                })
                            }
                        }
                    }
                    else if(this.normsList.length == 3) {   //3
                        this.doneQuotesList.specIds = [this.normsList[0].specId, this.normsList[1].specId, this.normsList[2].specId]
                        this.doneQuotesList.specNames = [this.normsList[0].specName, this.normsList[1].specName, this.normsList[2].specName]
                        for(let i=0; i<this.normsList[0].specValues.length; i++) {
                            for(let j=0; j<this.normsList[1].specValues.length; j++) {
                                for(let k=0; k<this.normsList[2].specValues.length; k++) {
                                    list.push({
                                        specValues: [this.normsList[0].specValues[i], this.normsList[1].specValues[j], this.normsList[2].specValues[k]],
                                    })
                                }
                            }
                        }
                    }
                    for(let i=0, len=this.doneQuotesList.specPrices.length; i<len; i++) {
                        this.doneQuotesList.specPrices[i].specValues = list[i].specValues
                    }
                    if(JSON.stringify(oldValue) !== "{}" && oldValue.specPrices.length > 0) {   //如果oldValue是单个  没有规格属性的 直接跳出  多个的时候有限制
                        for(let i=0, len=this.doneQuotesList.specPrices.length; i<len; i++) {
                            for(let j=0, len=oldValue.specPrices.length; j<len; j++) {
                                if(this.doneQuotesList.specPrices[i].specValues.join(',') == oldValue.specPrices[j].specValues.join(',')) {
                                    this.doneQuotesList.specPrices[i] = oldValue.specPrices[j]
                                    break
                                }
                            }
                            //新增 如果全选默认全选第一个
                            if(this.proPriceCheckedState) {
                                this.doneQuotesList.specPrices[i].proPrice = this.doneQuotesList.specPrices[0].proPrice
                                if(this.doneQuotesList.specPrices[i].usedIntegral === '') {
                                    this.doneQuotesList.specPrices[i].usedIntegral = this.doneQuotesList.specPrices[i].proPrice
                                }
                            }
                            if(this.oldPriceCheckedState) {
                                this.doneQuotesList.specPrices[i].marketPrice = this.doneQuotesList.specPrices[0].marketPrice
                            }
                        }
                    }
                }
                else {
                    this.doneQuotesList = this.copyQuotesInfo
                }
                this.$emit('update:doneQuotesList', this.doneQuotesList)
            },
            copyQuotesInfo(newValue, oldValue) {
                this.doneQuotesList = newValue
            }
        }
    }
</script>

<style lang="scss" scoped>
    $totalWidth: 1020;
    @function getW($px, $divWidth) {
        @return percentage($px / $divWidth);
    }
    #quotes {
        text-align: center;
        i {
            color: rgb(255, 97, 97);
        }
        table {
            width: 100%;
            th {
                height: 50px;
                line-height: 50px;
                position: relative;
            }
            td {
                height: 40px;
                line-height: 40px;
            }
        }
        table, th, tr, td {
            border: 1px solid #ccc;
        }
        table.all {
            .norm_fix {
                width: getW(80, $totalWidth);
            }
            .n_promotion {
                width: getW(160, $totalWidth);
                &>span {
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: -10px;
                    margin: auto;
                }
                .pro_price_checked {
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: -10px;
                    margin: auto;
                }
            }
            .n_integral {
                width: getW(160, $totalWidth);
            }
            .n_original {
                width: getW(160, $totalWidth);
                &>span {
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: -10px;
                    margin: auto;
                }
                .old_price_checked {
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: -10px;
                    margin: auto;
                }
            }
            .n_commodity {
                width: getW(145, $totalWidth);
            }
            .n_image {
                width: getW(145, $totalWidth);
            }
        }
        table.only {
            width: 600px;
        }
    }
</style>



